* {
    margin: 0;
    padding: 0px;
    box-sizing: border-box;
}

html,
body {
    min-height: 100vh;
    font-size: 100%;
    font-size: 14px;
}

.js-homework {
    background-image: url(../assets/fon_seryj_abstraktsiya_svetlyj_95691_1280x720.jpg);
    background-position: center center;
    background-size: cover;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

ul li {
    list-style: none;
}

.js-item {
    border: 2px solid #000000;
    margin-bottom: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 50px;
    padding-left: 20px;
    padding-right: 20px;
}

button {
    height: 40px;
    width: 100px;
    background-color: #9cdbfa;
}